import React, { useState } from 'react';
import './confirmmodal.css';
import axios from 'axios';
import { ChatState } from '../../context/ChatProvider';
import { toast } from 'react-toastify';
import { CircularProgress } from '@mui/material';

const ConfirmModal = ({ setFetchAgain, fetchAgain, setShowConfirmModal }) => {

    const [loading, setLoading] = useState(false);
    const { user, currentChat, setCurrentChat } = ChatState();

    const handleRemoveUser = async () => {
        try {
            setLoading(true);
            const config = {
                headers: {
                    Authorization: `Bearer ${user.authToken}`,
                },
            };

            await axios.post('/chat/deleteChat', { chatId: currentChat._id }, config);
            setFetchAgain(!fetchAgain);
            setCurrentChat();
            toast.success(`${currentChat.isGroupChat ? 'Group left' : 'Chat deleted'}`, {
                autoClose: 2000,
                theme: 'dark',
            });
            setShowConfirmModal(false);
            setLoading(false);
        }
        catch (error) {
            console.log(error);
            setLoading(false);
        }
    };

    return (
        <div className="confirmModal">
            <span>{`Are you sure you want to ${currentChat?.isGroupChat ? 'leave' : 'delete'}`}</span>
            <div className="button-container">
                <button
                    disabled={loading}
                    className={`${loading ? 'loading-' : ''}modal-btn`}
                    id='btn1'
                    onClick={() => { setShowConfirmModal(false) }}>
                    Cancel
                </button>
                <button
                    disabled={loading}
                    className={`${loading ? 'loading-' : ''}modal-btn`}
                    id='btn2'
                    onClick={handleRemoveUser}>
                    {loading ? <CircularProgress size={20} color="inherit" /> : 'Confirm'}
                </button>
            </div>
        </div>
    );
};

export default ConfirmModal;
